body.body-overflow {
	overflow: hidden;
}
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	background-color: #ffffff;
	.page {
		flex: 1 1 auto;
		padding: 130px 0 0;
	
		@media (max-width: 1919px) {
			padding: 100px 0 0;
		}
	
		@media (max-width: 1439px) {
			padding: 70px 0 0;
		}
		@media (max-width: 1024px) {
			padding: 6.836vw 0 0;
		}
		@media (max-width: 767px) {
			padding: 12vw 0 0;
		}
		&-main, &-trend {
			padding: 0!important;
		}
	}
}

.container {
	max-width: 100%;
	width: 100%;
	margin: 0 auto;
	padding: 0 30px;
	@media (max-width: 1919px){
		padding: 0 20px;
	}
	@media (max-width: 1439px){
		padding: 0 30px;
	}
	@media (max-width: 1024px) {
		padding: 0 2.930vw;
	}
	@media (max-width: 767px){
		padding: 0 5.333vw;
	}
}

.page-header {
	border-top: 1px solid rgba($color: #000000, $alpha: 0.2);
	padding-top: 30px;
	padding-bottom: 30px;
	min-height: 325px;
	@media (max-width: 1919px) {
		min-height: 245px;
	}

	@media (max-width: 1024px) {
		border-top: 0.098vw solid rgba($color: #000000, $alpha: 0.2);
		padding-bottom: 10.742vw;
		min-height: 6vw;
	}
	@media (max-width: 767px) {
		border-top: 0.267vw solid rgba($color: #000000, $alpha: 0.2);
		padding-top: 8vw;
		padding-bottom: 8vw;
		min-height: 29.948vw;
	}
}

.general-title {
	font-family: 'Suisse Intl';
	font-style: normal;
	font-weight: 450;
	font-size: 80px;
	line-height: 100.8%;
	letter-spacing: -0.03em;
	color: #000000;
	@media (max-width: 1919px){
		font-size: 60px;
		line-height: 90.8%;
	}
	@media (max-width: 1439px){
		font-size: 40px;
		line-height: 102.8%;
	}
	@media (max-width: 1024px){
		font-size: 3.906vw;
		line-height: 95.8%;
	}
	@media (max-width: 767px){
		font-size: 7.733vw;
	}
	span {
		color: #0047FF;
		font-weight: 500;
	}
	a {
		color: #0047FF;
		font-weight: 500;
		transition: all 0.5s ease 0s;
		position: relative;
	
		&::before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			right: 0;
			height: 6px;
			background-color: #0047FF;
			opacity: 0;
			transition: all 0.5s ease 0s;
	
			@media (max-width: 1919px) {
				height: 4px;
			}
	
			@media (max-width: 1439px) {
				height: 2px;
			}
			@media (max-width: 1024px) {
				height: 0.195vw;
			}
			@media (max-width: 767px){
				height: 0.533vw;
			}
		}
	
		&:hover {
			&::before {
				opacity: 1;
			}
		}
	}
}
.link-hv {
	transition: all 0.5s ease 0s;
	&:hover{
		color: #0047FF;
	}
}
.blue-btn {
			width: 595px;
			height: 65px;

			@media (max-width: 1919px){
				width: 595px;
				height: 65px;
			}
			@media (max-width: 1439px){
				width: 446px;
			}
			@media (max-width: 1024px){
				width: 29.492vw;
				height: 6.445vw;
			}
			@media (max-width: 767px){
				width: 100%;
				height: 12.800vw;
				flex: 1 1 100%;
			}
	cursor: pointer;
	background-color: #0047FF;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.5s ease 0s;
	
	&:hover {
		background-color: #0B3EC4;
	}
	
	span {
		font-family: 'Suisse Intl';
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		line-height: 21px;
		text-transform: uppercase;
		color: #FFFFFF;
	
		@media (max-width: 1919px) {
			font-size: 13.5094px;
			line-height: 16px;
		}
		@media (max-width: 1024px) {
			font-size: 1.367vw;
			line-height: 1.563vw;
		}
		@media (max-width: 767px) {
			font-size: 3.200vw;
			line-height: 3.733vw;
		}
	}
}
.general-text {
	font-family: 'Suisse Intl';
	font-style: normal;
	font-weight: 400;
	font-size: 18px;
	line-height: 21px;
	color: #000000;
	opacity: 0.5;
	@media (max-width: 1919px){
		font-size: 13.501px;
		line-height: 19px;
	}
	@media (max-width: 1439px){
		font-size: 14px;
		line-height: 20px;
	}
	@media (max-width: 1024px) {
		font-size: 1.367vw;
		line-height: 1.563vw;
	}
	@media (max-width: 767px){
		font-size: 3.200vw;
		line-height: 4.533vw;
	}
}

.flex-lr {
	width: 100%;
	display: flex;
	justify-content: space-between;
	position: relative;
	@media (max-width: 767px){
		flex-wrap: wrap;
	}
	.flex-lr__left {
		flex: 1 1 auto;
	}
	.flex-lr__right {
		flex: 0 0 65%;
		@media (max-width: 1919px) {
			flex: 0 0 64%;
		}
		@media (max-width: 1439px) {
			flex: 0 0 66%;
		}
		@media (max-width: 767px){
			flex: 1 0 100%;
		}
	}
}


.items-projects {
	display: flex;
	flex-wrap: wrap;
	margin: -20px -7.5px 0;
	@media (max-width: 1919px){
		margin: -10px -5px 0;
	}
	@media (max-width: 1439px){
		margin: -15px -7.5px 0;
	}
	@media (max-width: 1024px) {
		margin: -1.465vw -0.732vw 0;
	}
	@media (max-width: 767px) {
		margin: -4vw -2vw 0;
	}
	.item-projects {
		padding: 0 10px;
		flex: 0 1 50%;
		font-family: 'Suisse Intl';
		font-style: normal;
		@media (max-width: 1919px){
			padding: 0 5px;
		}
		@media (max-width: 1439px){
			padding: 0 7.5px;
		}
		@media (max-width: 1024px){
			padding: 0 0.732vw;
		}
		@media (max-width: 767px) {
			flex: 1 1 100%;
			padding: 0 2vw;
		}
		&.item-projects-dark {
			.item-projects__title {
				color: #ffffff;
			}
			.item-projects__text {
				color: #ffffff;
			}
		}
		&__content {
			margin-top: 20px;
			display: block;
			position: relative;
			min-height: 920px;
			padding: 20px;
			overflow: hidden;
			@media (max-width: 1919px){
				margin-top: 10px;
				min-height: 670px;
			}
			@media (max-width: 1439px){
				min-height: 480px;
				margin-top: 15px;
			}
			@media (max-width: 1024px){
				min-height: 47.070vw;
				margin-top: 1.465vw;
				padding: 1.953vw;
			}
			@media (max-width: 767px){
				margin-top: 4vw;
				min-height: 97.067vw;
				padding: 2.667vw;
			}
			.item-projects-bronze {
				position: absolute;
				left: 30px;
				bottom: 30px;
				width: 128px;
				height: 128px;
				@media (max-width: 1919px){
					width: 96px;
					height: 96px;
					left: 15px;
					bottom: 15px;
				}
				@media (max-width: 1439px){
					display: none;
				}
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
		&__header {
			position: relative;
			z-index: 2;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: flex-start;
		}
			&__title {
				max-width: 50%;
				font-weight: 450;
				font-size: 48px;
				line-height: 108.8%;
				letter-spacing: -0.03em;
				color: #2A2A2A;
				transition: all 0.5s ease 0s;
				@media (max-width: 1919px){
					max-width: 50%;
					font-size: 36.025px;
				}
				@media (max-width: 1439px){
					font-size: 28px;
					line-height: 32px;
				}
				@media (max-width: 1024px) {
					font-size: 2.734vw;
					line-height: 3.125vw;
				}
				@media (max-width: 767px){
					font-size: 5.333vw;
					line-height: 108.8%;
				}
			}
			
			&__btns {
				display: flex;
				align-items: center;
				position: absolute;
				top: 5px;
				right: 5px;
				@media (max-width: 1024px){
					top: 0.488vw;
					right: 0.488vw;
				}
				@media (max-width: 767px){
					flex-direction: column;
					align-items: flex-end;
					top: 1.333vw;
					right: 1.333vw;
				}
			}
				&__btn {
					font-weight: 400;
					font-size: 18px;
					line-height: 21px;
					text-align: right;
					color: #000000;
					background: #FFFFFF;
					border-radius: 25px;
					display: flex;
					justify-content: center;
					align-items: center;
					padding: 10px 15px;
					margin-right: 10px;
					@media (max-width: 1919px){
						font-size: 13.5094px;
						line-height: 16px;
						border-radius: 18.763px;
						padding: 8px 12px;
					}
					@media (max-width: 1439px){
						border-radius: 25px;
						padding: 6px 10px;
					}
					@media (max-width: 1024px) {
						font-size: 1.367vw;
						line-height: 1.563vw;
						border-radius: 2.441vw;
						padding: 0.781vw 0.781vw;
						margin-right: 0.977vw;
					}
					@media (max-width: 767px){
						font-size: 3.733vw;
						line-height: 4.267vw;
						margin-right: 0;
						border-radius: 6.667vw;
						padding: 1.600vw 2.667vw;
						&:not(:first-child) {
							margin-top: 1.333vw;
						}
					}
				}
			
			&__text {
				max-width: 50%;
				position: relative;
				z-index: 2;
				font-weight: 400;
				font-size: 18px;
				line-height: 21px;
				color: #000000;
				opacity: 0.5;
				flex: 1 1 100%;
				margin-top: 8px;
				br {
					display: none;
				}
				@media (max-width: 1919px){
					font-size: 13.5094px;
					line-height: 16px;
					margin-top: 12px;
				}
				@media (max-width: 1919px) {
					font-size: 1.367vw;
					line-height: 1.563vw;
					margin-top: 1.172vw;
				}
				@media (max-width: 767px){
					font-size: 3.200vw;
					line-height: 3.733vw;
					margin-top: 3.200vw;
				}
			}
		
		&__img {
			display: flex;
			justify-content: center;
			img {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
				transition: all 0.5s ease 0s;
			}
		}
		&:hover{
			.item-projects__title {
				color: #0047FF;
			}
			.item-projects__img {
				img {
					transform: scale(1.1);
				}
			}
		}
	}


	.item-projects-content-grey {
		background-color: #F4EAEA;
		&-dark {
			background-color: #E4E4E4;
		}
	}

	.item-projects-content-blue {
		background-color: #E3F0FE;
	}

	.item-projects-big {
		flex: 1 1 100%;
		.item-projects__img {
		}
	}
}










